<template>
  <v-sheet :color="color" class=" px-3 mb-3 mx-3 " width="100%">
         <v-row>
           <v-col class="title">{{title}}</v-col>
           <v-col cols="3"><v-select  dense outlined v-model="selected" :items="[{text: 'Today', value:'today'}, {text: 'This month', value:'month'}]" item-text="text" item-value="value" hide-details ></v-select></v-col>
         </v-row>

        <v-row>
          <v-col class="mx-2 pa-0 px-2" v-for="(item, ind) in (selected == 'month' ? items : items2)" :key="ind">
            <fieldset class="px-2 mb-2 fielset">
              <legend>{{ item.title }}</legend>
              <v-row>
                <v-col cols="3"><small>FT"D</small><h2>{{ item.ftd || 0 }}</h2></v-col>
                <v-col> <small>Total</small><h2>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.total || 0)}}</h2></v-col>
              </v-row>
             </fieldset>
          </v-col>  
        </v-row>
  </v-sheet>
</template>
<script>
 
export default {
  name: 'TotalSheet',
  props: {
    title: String,
    items: Array,  
    items2: Array,  
    color: {
      type: String,
      default: 'yellow'
    }
  },
  data: () => ({
    selected: 'month'
  }) 
}
</script>
<style   scoped>
.fielset {
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 5px;
}
</style>