<template>
  <v-content class="pa-0 ma-0">
 
    <v-row>
      <v-col><app-panel title="Today revenue" :count="total.today.ftd" :amount="total.today.amount" /></v-col>
      <v-col><app-panel title="This month revenue" :count="total.month.ftd" :amount="total.month.amount" /></v-col>
    </v-row>
     <v-row>
        

      <app-total-sheet color="blue" title="English" :items="[
            { 
              title: 'Sales',
              ftd: totalMonth.sales.en.ftd,
              total: totalMonth.sales.en.total
            },
            { 
              title: 'Retentions',
              ftd: totalMonth.retention.en.ftd,
              total: totalMonth.retention.en.total
            },
            { 
              title: 'Total',
              ftd: totalMonth.sales.en.ftd+totalMonth.retention.en.ftd,
              total: totalMonth.sales.en.total+totalMonth.retention.en.total
            }
          ]" :items2="[
            { 
              title: 'Sales',
              ftd: totalToday.sales.en.ftd,
              total: totalToday.sales.en.total
            },
            { 
              title: 'Retentions',
              ftd: totalToday.retention.en.ftd,
              total: totalToday.retention.en.total
            },
            { 
              title: 'Total',
              ftd: totalToday.sales.en.ftd+totalToday.retention.en.ftd,
              total: totalToday.sales.en.total+totalToday.retention.en.total
            }
          ]"></app-total-sheet>
           
    </v-row>
    <v-row>
      <app-total-sheet color="orange"
        title="Arabic" :items="[
          { 
            title: 'Sales',
            ftd: totalMonth.sales.ar.ftd,
            total: totalMonth.sales.ar.total
          },
          { 
            title: 'Retentions',
            ftd: totalMonth.retention.ar.ftd,
            total: totalMonth.retention.ar.total
          },
          { 
            title: 'Total',
            ftd: totalMonth.sales.ar.ftd+totalMonth.retention.ar.ftd,
            total: totalMonth.sales.ar.total+totalMonth.retention.ar.total
          }
        ]" :items2="[
            { 
              title: 'Sales',
              ftd: totalToday.sales.ar.ftd,
              total: totalToday.sales.ar.total
            },
            { 
              title: 'Retentions',
              ftd: totalToday.retention.ar.ftd,
              total: totalToday.retention.ar.total
            },
            { 
              title: 'Total',
              ftd: totalToday.sales.ar.ftd+totalToday.retention.ar.ftd,
              total: totalToday.sales.ar.total+totalToday.retention.ar.total
            }
          ]"
      ></app-total-sheet>
    </v-row>
    <v-row>
      <app-total-sheet title="Label1" :items="[
            { 
              title: 'Sales',
              ftd: totalMonth.sales.lb.ftd,
              total: totalMonth.sales.lb.total
            },
            { 
              title: 'Retentions',
              ftd: totalMonth.retention.lb.ftd,
              total: totalMonth.retention.lb.total
            },
            { 
              title: 'Total',
              ftd: totalMonth.sales.lb.ftd+totalMonth.retention.lb.ftd,
              total: totalMonth.sales.lb.total+totalMonth.retention.lb.total
            }
          ]" :items2="[
            { 
              title: 'Sales',
              ftd: totalToday.sales.lb.ftd,
              total: totalToday.sales.lb.total
            },
            { 
              title: 'Retentions',
              ftd: totalToday.retention.lb.ftd,
              total: totalToday.retention.lb.total
            },
            { 
              title: 'Total',
              ftd: totalToday.sales.lb.ftd+totalToday.retention.lb.ftd,
              total: totalToday.sales.lb.total+totalToday.retention.lb.total
            }
          ]"></app-total-sheet>
    </v-row>
  </v-content>
</template>

<script>
import AppPanel from './../components/dashboard/Panel'
import AppTotalSheet from './../components/dashboard/TotalSheet'
import { mapActions } from 'vuex'

export default {
  name: 'Dashboard',
  components: {
    AppPanel,
    AppTotalSheet
  },
  data: () => ({
    totalMonth: {
        sales: {
          en: { ftd: 0, total: 0 },
          ar: { ftd: 0, total: 0 },
          lb: { ftd: 0, total: 0 }
        },
        retention: {
          en: { ftd: 0, total: 0 },
          ar: { ftd: 0, total: 0 },
          lb: { ftd: 0, total: 0 }
        }
    },
    totalToday: {
        sales: {
          en: { ftd: 0, total: 0 },
          ar: { ftd: 0, total: 0 },
          lb: { ftd: 0, total: 0 }
        },
        retention: {
          en: { ftd: 0, total: 0 },
          ar: { ftd: 0, total: 0 },
          lb: { ftd: 0, total: 0 }
        }
    },
    total: {
      today: {
        ftd: 0,
        amount: 0
      },
      month: {
        ftd: 0,
        amount: 0
      }
    }
  }),
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
  },
  async created () {
    const monthTotal = await this.get({ collection: 'dashboard/month' }) 
    const todayTotal = await this.get({ collection: 'dashboard/today' }) 
    if (monthTotal && monthTotal.status == 200) {
      this.total.month.ftd = (monthTotal.data.rtsAll[0] ? monthTotal.data.rtsAll[0].count :  0) + (monthTotal.data.salesAll[0] ? monthTotal.data.salesAll[0].count :  0)
      this.total.month.amount = (monthTotal.data.rtsAll[0] ? monthTotal.data.rtsAll[0].amount :  0) + (monthTotal.data.salesAll[0] ? monthTotal.data.salesAll[0].amount :  0)
      monthTotal.data.sales.forEach(e => {
        this.totalMonth.sales[e._id].ftd = e.count 
        this.totalMonth.sales[e._id].total = e.amount 
      })
      monthTotal.data.rts.forEach(e => {
        this.totalMonth.retention[e._id].ftd = e.count 
        this.totalMonth.retention[e._id].total = e.amount 
      })
    
    }
    if (todayTotal && todayTotal.status == 200) {
      this.total.today.ftd = (todayTotal.data.rtsAll[0] ? todayTotal.data.rtsAll[0].count :  0) + (todayTotal.data.salesAll[0] ? todayTotal.data.salesAll[0].count :  0)
      this.total.today.amount = (todayTotal.data.rtsAll[0] ? todayTotal.data.rtsAll[0].amount :  0) + (todayTotal.data.salesAll[0] ? todayTotal.data.salesAll[0].amount :  0)
      
      todayTotal.data.sales.forEach(e => {
        this.totalToday.sales[e._id].ftd = e.count 
        this.totalToday.sales[e._id].total = e.amount 
      })
      todayTotal.data.rts.forEach(e => {
        this.totalToday.retention[e._id].ftd = e.count 
        this.totalToday.retention[e._id].total = e.amount 
      })
   }

  }
}

 
</script>